import React from "react";
import {useState} from "react";
import "./Glowingglassbuttons.css"
import { Box } from "@chakra-ui/react"


function Glowingglassbuttons(props) {

  const [isShown, setIsShown] = useState(props.BgColor);


  return (
    <>
<Box 

style={props.style }



boxShadow={`0 1px 2px rgb(0 0 0 / 10%), 0 3px 3px rgb(0 0 0 / 10%), 0 6px 6px rgb(0 0 0 / 20%), 0 0 8px 1px transparent, inset 0 0 2px 4px ${props.BgColor}`}
_hover={{  boxShadow: `0 1px 2px rgba(0, 0, 0, 0.1), 0 3px 3px rgba(0, 0, 0, 0.1), 0 6px 6px rgba(0, 0, 0, 0.2), 0 0 8px 1px ${props.BgColor+50}, inset 0 0 2px 4px ${props.BgColor}`}}

onMouseEnter={() => setIsShown(props.OnColor)}
onMouseLeave={() => setIsShown(props.BgColor)}


_after={{backgroundColor:isShown }}

_before={{backgroundColor:isShown }}
  className="button" ><span>{props.children}</span></Box>



    </>
  );
}

export default Glowingglassbuttons;