import React from "react";
import { Box } from "@chakra-ui/react";
import "./magicalholder.css"


function magicalholder(props) {




  return (
    <>

 
<Box h={props.height} w={props.width} style={props.style} className="card"    _after={{

top: `calc(${props.height} / 6)`,
filter: `blur(calc(${props.height}/ 6))`,
backgroundImage: `linear-gradient(
  var(--rotate)
  , ${props.color1}, ${props.color2}, ${props.color3})`
}} 



_before= {{
  borderRadius: `${props.borderRadius}`,
  backgroundImage: `linear-gradient(var(--rotate), ${props.color4}, ${props.color5}, ${props.color6})`

}}



>{props.children}</Box>

    </>
  );
}

export default magicalholder;




