import React from "react";
import "./glasscard.css"


function glasscard(props) {


if(props.href){let hr=`href="${props.href}"`} else{ const hr=null}

  return (
    <>

    <div style={props.styleOFcard} class="glass-card-card">
      <div style={props.styleOFbox} class="glass-card-box">
        <div style={props.styleOFcontent} class="glass-card-content">
          <center style={props.styleOFlogo} className= "glass-card-fab" >
          {props.logo}
          </center>
  <h3 style={props.styleOFheader} className="glass-card-h3"  >{props.header}</h3>
          <p style={props.styleOFtext} className="glass-card-p" >
          {props.text}
          </p>

          {props.children}
        </div>
      </div>
    </div>


    </>
  );
}

export default glasscard;