import React from "react";
import {useState} from "react";
import "./glasslogin.css"
import {  Box} from "@chakra-ui/react"
import { motion ,useCycle} from "framer-motion"



function Glasslogin(props) {



  const variants3 = {
    visible1: { opacity: 1 , rotateY: 360 ,transition: { duration: 1 }},
    visible2:  { opacity: 1  ,rotateY: -360 ,transition: { duration: 1 }},
  }



const [animation ,animationcycle]=useCycle('visible1','visible2')


const [Z1, setZ1] = useState(1)
const [Z2, setZ2] = useState(-1)

function change() {
  
  animationcycle()

setTimeout(() => {
  setZ1( Z1 * -1  )
  setZ2( Z2 * -1  )
}, 500);
  

 
}




  return (
    <>
 
 <Box zIndex={Z1}  position="absolute"   width="100%" >
<motion.div   animate={animation} variants={variants3} >
    <Box h={props.h}  w={{
  sm: "80%",
  nk:"80%",
  md: "70%",
  
  lg: "70%",
  xl: "55%",

}} className="container">
  
        <div className="form">
            <h2>Login Form</h2>
            <form >
            <div class="inputBox">

            <input className="input" type="text" name="" placeholder="Username"/>
         
            <span class="line"></span>
            </div>

            <div class="inputBox">
            <input className="input" type="password" name="" placeholder="password"/>
            <span class="line"></span>
            </div>
            
            <div class="inputBox">
<center>
            <input type="submit" value="Login"/>
            </center>
            <Box h="1rem" ></Box>


            <p>Forgot password ?<a href="#"> reset your password.</a></p>
      <Box h="1rem" ></Box>
                    <p>You don't have an account ?<a  onClick={()=> change() }  style={{cursor:"pointer"}}  > create one.</a></p>

            </div>


            </form>
        </div>
      
    </Box>
    </motion.div>
    </Box>


    <Box  zIndex={Z2} position="absolute"   width="100%" >
    <motion.div   animate={animation} variants={variants3} >
    <Box h={props.h}  w={{
  sm: "80%",
  nk:"80%",
  md: "70%",
  
  lg: "70%",
  xl: "55%",

}} className="container">
  
        <div className="form">
            <h2>sing up</h2>
            <form >
            <div class="inputBox">

            <input className="input" type="text" name="" placeholder="Username"/>
         
            <span class="line"></span>
            </div>

            <div class="inputBox">

<input className="input" type="Email" name="" placeholder="Email"/>

<span class="line"></span>
</div>

            <div class="inputBox">
            <input className="input" type="password" name="" placeholder="password"/>
            <span class="line"></span>
            </div>
            
            <div class="inputBox">
<center>
            <input type="submit" value="SingUp"/>
            </center>
            <Box h="1rem" ></Box>


           
      <Box h="1rem" ></Box>
                    <p>You have an account ?<a onClick={()=>change()}  style={{cursor:"pointer"}}  > login</a></p>

            </div>


            </form>
        </div>
      
    </Box>
    </motion.div>

</Box>

    </>
  );
}

export default Glasslogin;